body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.info{display:flex;flex-direction:column;align-content:center;justify-content:center;height:550px;padding:50px 200px;background:#f6f6f6;font-size:18pt;font-family:'Ubuntu', sans-serif;opacity:1}.info .name{font-size:50pt;padding:10px 0}.info .school{display:inline-block;font-family:'Ubuntu', sans-serif;font-size:36pt;width:auto;margin:0;padding:0}.info .dept{font-size:28pt;padding-bottom:40px}.info .miliday{font-size:28pt;padding:10px 0}.info .miliday .percent{font-size:18pt;color:green;padding:5px 0}

@keyframes smoothAppear{from{opacity:0;transform:translateY(-5%)}to{opacity:1;transform:translateY(0)}}.header{display:block;position:fixed;top:0;background:#8785a2;height:50px;padding:5px 0px;width:100%;animation:smoothAppear 1s;z-index:1}.header .header-wrapper{height:45px;padding-left:40px;padding-right:40px;align-items:center;justify-content:center}.header .header-wrapper .title{display:inline-block;vertical-align:middle;height:45px;width:40%}.header .header-wrapper .title .titleBox{margin:5px 0;justify-content:center;text-align:left}.header .header-wrapper .title .titleBox .titleLink{display:block;color:white;text-decoration:none;font-size:18pt;font-family:'Gowun Batang', serif}.header .header-wrapper .link{display:inline-block;vertical-align:middle;height:45px;width:60%}.header .header-wrapper .link .linkBox{margin:5px 0;justify-content:center;text-align:right}.header .header-wrapper .link .linkBox .list{display:inline-block;margin:0 10px}.header .header-wrapper .link .linkBox .list .box{color:white;text-decoration:none;font-size:18pt;font-family:'Gowun Batang', serif}

.introduce{display:flex;flex-direction:column;align-content:center;justify-content:center;height:550px;padding-top:200px;padding-bottom:100px;background:#f6f6f6;font-size:18pt;font-family:'Noto Sans KR', sans-serif;text-align:center}.introduce .big-text{font-size:100pt;margin:0;padding:25px 0}.introduce .small-text{font-size:25pt;margin:0;padding:25px 0}.introduce .interest{padding:25px 0;text-align:left;font-size:20pt;display:flex;flex-direction:column;justify-content:center;align-content:center;margin:0}

.DevStack{background:#f6f6f6;padding:100px 100px}.contents{display:flex;padding:0 20px;font-size:18pt;font-family:'Noto Sans KR', sans-serif;opacity:1}.contents .cmp{padding:30px;display:flex;flex-direction:column;align-content:center;justify-content:center;vertical-align:center;text-align:center}.icon{width:36px;height:36px;margin:0 auto}.title{font-size:24pt}

.dogSound{display:flex;flex-direction:column;align-content:center;justify-content:center;height:auto;padding:50px 200px;background:#f6f6f6;font-family:'Noto Sans KR', sans-serif}.dogSound .wrapper{padding:100px 0px}.dogSound .wrapper .big{font-size:80pt;padding:10px 0}.dogSound .wrapper .small{font-size:20pt;padding:10px 0;padding-bottom:50px}.dogSound .wrapper .small.reallyDogSound{font-weight:bold;font-style:oblique}.dogSound .wrapper .text{font-size:18pt}

.wrapper{background:#F6F6F6}

